* {
  font-family: Lato, sans-serif;
}

body {
  background: #7a7a7ac9 !important;
}

ul {
  padding-left: 0;
  list-style: none;
}

.logo {
  color: #d04f4f;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 42px;
  padding-bottom: 17px;
  display: flex;
}

.search-panel {
  background: #e7e7e7;
  border-radius: 5px;
  align-items: center;
  padding: 10px 24px;
  font-size: 18px;
  display: flex;
}

.search-panel__input {
  width: 100%;
  color: #00000040;
  background: none;
  border: none;
  outline: #0000;
  font-size: 18px;
}

.search-panel__btn {
  background: none;
  border: none;
  outline: #0000;
  font-size: 24px;
}

.filters {
  z-index: 1;
  gap: 50px;
  display: flex;
  position: absolute;
}

.filter {
  width: 170px;
  border-radius: 5px;
  align-self: flex-start;
  transition: all .3s;
  position: relative;
  overflow: hidden;
}

.filter__arrow {
  cursor: pointer;
  width: 12px;
  height: 12px;
  border-bottom: 2px solid #fff;
  border-left: 2px solid #fff;
  position: absolute;
  top: 23px;
  right: 20px;
  transform: rotate(-45deg);
}

.filter__input {
  width: 100%;
  color: #fff;
  background: none;
  border: none;
  outline: #0000;
  padding: 20px 50px 20px 20px;
}

.filter__input::placeholder {
  color: #fff;
}

.filter__list {
  max-height: 400px;
  padding: 0 5px 10px;
  display: none;
  overflow-y: scroll;
}

.filter__option-wrap {
  width: 100%;
}

.filter__option {
  cursor: pointer;
  width: 100%;
  color: #fff;
  border-radius: 5px;
  padding: 5px 15px;
  transition: all .3s;
  display: block;
}

.filter__option:hover, .filter__option.active {
  color: #000;
  background: #fff;
}

.filter_blue {
  background: #3282f7;
}

.filter_green {
  background: #68d9a4;
}

.filter_tomato {
  background: #ed6454;
}

.filter.open {
  width: 670px;
}

.filter.open .filter__arrow {
  top: 27px;
  transform: rotate(135deg);
}

.filter.open .filter__input::placeholder {
  color: #ffffff40;
}

.filter.open .filter__list {
  flex-wrap: wrap;
  display: flex;
}

.filter.open .filter__option-wrap {
  width: 33%;
  padding: 5px;
}

.filter.open .filter__option {
  padding: 10px 15px;
}

.badges {
  flex-wrap: wrap;
  align-items: center;
  gap: 5px 10px;
  padding-top: 25px;
  padding-bottom: 15px;
  display: flex;
}

.badges .badge {
  cursor: pointer;
}

.badges > span {
  cursor: auto;
  color: #fff;
  align-items: center;
  transition: all .3s;
  display: flex;
}

.badges > span:hover {
  color: #fff;
}

.badges .badge-blue {
  background: #3282f7;
}

.badges .badge-blue:hover {
  background: #0067ff;
}

.badges .badge-green {
  background: #68d9a4;
}

.badges .badge-green:hover {
  background: #15d57a;
}

.badges .badge-tomato {
  background: #ed6454;
}

.badges .badge-tomato:hover {
  background: #e83621;
}

.badges i {
  font-size: 22px;
}

.recipes {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 50px 20px;
  margin-top: 100px;
  display: flex;
}

.recipe {
  width: 380px;
  background: #e7e7e7;
  border-radius: 5px;
  overflow: hidden;
}

.recipe__wrap {
  padding: 0 20px 20px;
}

.recipe__header {
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

.recipe__title {
  font-size: 18px;
}

.recipe__duration {
  align-items: center;
  gap: 5px;
  display: flex;
}

.recipe__duration i {
  color: tomato;
  background: #000;
  border-radius: 50%;
  font-size: 20px;
}

.recipe__content {
  justify-content: space-between;
  font-size: 12px;
  display: flex;
}

.recipe__description {
  width: 50%;
  height: 90px;
  text-overflow: ellipsis;
  margin: 0;
  overflow: hidden;
}

